import { Injectable } from '@angular/core';

/**
 * Utility service for DOM manipulations
 */
@Injectable({
  providedIn: 'root',
})
export class DomAttributesService {
  static nextId = 0;
  private idPrefix = 'bb_element_';

  public generateId() {
    return `${this.idPrefix}${DomAttributesService.nextId++}`;
  }
}
